@import '../../style/variables.scss';

.char {
	&__content {
		margin-top: 50px;
        display: grid;
        grid-template-columns: 650px 425px;
        column-gap: 25px;
        align-items: start;
	}
	&__grid {
		display: grid;
		grid-template-columns: repeat(3, 200px);
		column-gap: 25px;
        row-gap: 30px;
	}
	&__item {
		width: 200px;
		height: 318px;
		background-color: $dark;
		box-shadow: 5px 5px 10px rgba(0, 0, 0, .25);
		padding: 15px;
		cursor: pointer;
        transition: 0.3s transform;
        img {
            width: 200px;
            height: 200px;
            object-fit: cover;
            transform: translate(-15px, -15px);
        }
        &_selected {
            box-shadow: 0 5px 20px $red;
            transform: translateY(-8px);
        }
	}
	&__name {
		color: rgb(255, 255, 255);
		font-size: 22px;
		font-weight: 700;
		line-height: 26px;
		text-transform: uppercase;
	}
}